import React, { Component } from 'react';
import UserService from '../services/UserService';
import swal from 'sweetalert';
import {
  Label,
  Button,
  Row,
  Col,
  
} from 'reactstrap';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Home } from '@material-ui/icons';
import HomeComponent from './HomeComponent';
const required = (val) => val && val.length;
const maxLength = (len) => (val) => !val || val.length <= len;
const minLength = (len) => (val) => val && val.length >= len;
const isNumber = (val) => !isNaN(Number(val));
const validEmail = (val) =>
  /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);
class GetDetailsComponent extends Component {
  constructor(props) {
    super(props);
    this.state={
      success:false
    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleSuccess=this.handleSuccess.bind(this)
  
    console.log('con for');
  }
  handleSuccess(){
    this.setState({success:true})
  }
  componentDidMount() {
    console.log('mounted');
  }
  componentDidUpdate() {
    console.log('Updated');
  }
  componentWillUnmount() {
    console.log('unmount');
  }
 returnHome(){
   return(<HomeComponent/>)
 }
  handleSubmit(values) {
      console.log(values)
        UserService.postUser(values).then(response=>{console.log(response.data)
          swal({title: "Success", text: "You're Data Submitted Successfully", type: 
          "success"}).then(this.handleSuccess
          );       

      }).catch(error=>{console.log(error)
      swal("Failed","Data Not Submitted","error")
      })

       
    }
  
renderForm(){
  return (
    <div className="container">
      <div className="row row-content">
        <div className="col-12">
          <h3>Enter the details</h3>
        </div>
        <div className="col-12 col-md-9">
          <LocalForm onSubmit={(values) => this.handleSubmit(values)}>
            <Row className="form-group">
              <Label htmlFor="firstName" md={2}>
                First Name
              </Label>
              <Col md={10}>
                <Control.text
                  model=".firstName"
                  id="firstName"
                  name="firstName"
                  placeholder="First Name"
                  className="form-control"
                  validators={{
                    required,
                    minLength: minLength(3),
                    maxLength: maxLength(15),
                  }}
                />
                <Errors
                  className="text-danger"
                  model=".firstName"
                  show="touched"
                  messages={{
                    required: 'Required and ',
                    minLength: 'Must be greater than 2 characters',
                    maxLength: 'Must be 15 characters or less',
                  }}
                />
              </Col>
            </Row>
            <Row className="form-group">
              {' '}
              <Label htmlfor="lasttName" md={2}>
                Last Name
              </Label>
              <Col md={10}>
                <Control.text
                  model=".lastName"
                  id="lastName"
                  name="lasttName"
                  placeholder="Last Name"
                  className="form-control"
                  validators={{
                    required,
                    minLength: minLength(3),
                    maxLength: maxLength(15),
                  }}
                />
                <Errors
                  className="text-danger"
                  model=".lastName"
                  show="touched"
                  messages={{
                    required: 'Required and ',
                    minLength: 'Must be greater than 2 characters',
                    maxLength: 'Must be 15 characters or less',
                  }}
                />
              </Col>
            </Row>

            <Row className="form-group">
              <Label htmlfor="telNum" md={2}>
                Contact no
              </Label>
              <Col md={10}>
                <Control.text
                  model=".telNum"
                  id="telNum"
                  name="telNum"
                  placeholder="Contact Number"
                  className="form-control"
                  validators={{
                    required,
                    minLength: minLength(3) ,
                    maxLength: maxLength(15),
                    isNumber: isNumber ,
                  }}
                />
                <Errors
                  className="text-danger"
                  model=".telNum"
                  show="touched"
                  messages={{
                    required: 'Required and ',
                    minLength: 'Must be greater than 2 characters',
                    maxLength: 'Must be 15 characters or less',
                    isNumber: ' Must be a number',
                  }}
                />
              </Col>
            </Row>
            <Row className="form-group">
              <Label htmlfor="email" md={2}>
                Email
              </Label>
              <Col md={10}>
                <Control.text
                  model=".email"
                  id="email"
                  name="email"
                  placeholder="Email"
                  className="form-control"
                  validators={{
                    required,
                    validEmail,
                  }}
                />
                <Errors
                  className="text-danger"
                  model=".email"
                  show="touched"
                  messages={{
                    required: 'Required',
                    validEmail: 'Not a vaild Email',
                  }}
                />
              </Col>
            </Row>
            <Row className="form-group">
              <Label htmlfor="address" md={2}>
                Address
              </Label>
              <Col md={10}>
                <Control.textarea
                  model=".address"
                  id="address"
                  name="address"
                  rows="5"
                  className="form-control"
                />
              </Col>
            </Row>
            <Row className="form-group">
              <Label htmlfor="companyName" md={2}>
                Company Name
              </Label>
              <Col md={10}>
                <Control.text
                  model=".companyName"
                  id="companyName"
                  name="companyName"
                  placeholder="Company Name"
                  className="form-control"
                  validators={{
                    required,
                  }}
                />
                <Errors
                  className="text-danger"
                  model=".companyName"
                  show="touched"
                  messages={{
                    required: 'Required',
                    }}
                />
              </Col>
            </Row>
            <Row className="form-group">
              <Label htmlfor="designation" md={2}>
                Designation
              </Label>
              <Col md={10}>
                <Control.text
                  model=".designation"
                  id="designation"
                  name="designation"
                  placeholder="designation"
                  className="form-control"
                  validators={{
                    required,
                  }}
                />
                <Errors
                  className="text-danger"
                  model=".designation"
                  show="touched"
                  messages={{
                    required: 'Required',
                  }}
                />
              </Col>
            </Row>
            <Row className="form-group">
              <Label htmlfor="level" md={2}>
                Career Level
              </Label>
              <Col md={10}>
                <Control.text
                  model=".level"
                  id="level"
                  name="level"
                  placeholder="Level"
                  className="form-control"
                  validators={{
                    required,
                  }}
                />
                <Errors
                  className="text-danger"
                  model=".level"
                  show="touched"
                  messages={{
                    required: 'Required',
                  }}
                />
              </Col>
            </Row>
            <Row className="form-group">
              <Label htmlfor="salary" md={2}>
                Salary
              </Label>
              <Col md={10}>
                <Control.text
                  model=".salary"
                  id="salary"
                  name="salary"
                  placeholder="Salary in Rs"
                  className="form-control"
                  validators={{
                    required,
                  }}
                />
                <Errors
                  className="text-danger"
                  model=".salary"
                  show="touched"
                  messages={{
                    required: 'Required',
                  }}
                />
              </Col>
            </Row>
            <Row className="form-group">
              <Col md={{ size: 2, offset: 2 }}>
                <Button type="submit" color="success">
                  Submit
                </Button>
              </Col>
            </Row>
          </LocalForm>
        </div>
      </div>
    </div>
  );
}
  render() {
    console.log('render');
    return(
      <div>
          {!this.state.success && this.renderForm()}
          {this.state.success && this.returnHome()}
      </div>
    )
   
  }
}
export default GetDetailsComponent;