import axios from "axios"

class UserService{
    getUser(props){
        console.log(props+" deea")
        return axios.get(`http://localhost:8080/api/getEmployees/${props}`)
    }
    postUser(props){
        console.log(props)
        return axios.post('http://localhost:8080/api/postEmployee',props);
    }
}
export default new UserService();