import React from 'react'
import swal from 'sweetalert'
import { Form,Button, Card, CardBody, CardTitle, CardText } from 'reactstrap'
import UserService from '../services/UserService'

export class ViewOrupdateComponent extends React.Component{
    constructor(){
        super()
        this.state={
            id:'',
            emp:''
        }
        this.handleId=this.handleId.bind(this)
    }
    
   
     handleId(e){
       this.setState({id:e.target.value})
     }
     handleSubmit(){
         const id=this.state.id
         if(id)
         UserService.getUser(this.state.id).then(response=>this.setState({emp:response.data})).catch(error=>{swal("failed","Please enter valid empid","error")
        this.setState({id:''})})
        else
        swal("failed","Please enter valid empid","error")
     }

    renderForm(){
        return(
            <div className='d-flex justify-content-center'>
                <Form>
                    <label color='grey'>Enter the Employee Id </label><br></br>
                    <input type="text" value={this.state.id} onChange={this.handleId} ></input><br></br>
                    <Button color="dark" value="submit" onClick={this.handleSubmit.bind(this)}>View</Button>
                </Form>
            </div>
        )
    }
    renderEmployee(){
        const Emp=this.state.emp
        return(
            <div className='container-fluid'>
                <table className='table  table-bordered'>
                    <thead>
                        <tr>
                            <th>Employee Id</th>
                            <th>First Name</th>
                            <th>Last Name</th>
                            <th>Phone Number</th>
                            <th>Email</th>
                            <th>Address</th>
                            <th>Company Name</th>
                            <th>Designation</th>
                            <th>Career level</th>
                            <th>Salary</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>{Emp.id}</td>
                            <td>{Emp.firstName}</td>
                            <td>{Emp.lastName}</td>
                            <td>{Emp.telNum}</td>
                            <td>{Emp.email}</td>
                            <td>{Emp.address}</td>
                            <td>{Emp.companyName}</td>
                            <td>{Emp.designation}</td>
                            <td>{Emp.level}</td>
                            <td>{Emp.salary}</td>
                        </tr>
                    </tbody>
                </table>
                        
            </div>

        )
    }
    updateForm(){
        console.log("hi")
        return(
            <div>Hello</div>
        )
    }
    render(){
        return(
            <div>
                
                { !this.state.emp && this.renderForm()}
                {this.state.emp && this.renderEmployee()}
            
            </div>
        )
    }
}